import React from "react";
import "./Loader.css";

function Loader() {
  return (
    <div class="container">
      <div class="row">
        <div class="text-center">
          <h2>Creative Animated Loading icon in HTML5 CSS3</h2>
        </div>
        <div class="animationload">
          <div class="osahanloading"></div>
        </div>
      </div>
    </div>
  );
}

export default Loader;
